.college-documents{padding:40px 0;background-color:#fff;position:relative;overflow:hidden}.college-documents::before{content:"";position:absolute;left:0;top:0;width:100%;height:100%;background-repeat:repeat;opacity:.03;z-index:0}.college-documents .container{position:relative;z-index:1;width:100%;max-width:1200px;margin:0 auto;padding-left:24px;padding-right:24px}@media screen and (max-width: 450px){.college-documents .container{padding-left:16px;padding-right:16px}}.college-documents__header{display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-bottom:32px;padding-bottom:24px;border-bottom:1px solid rgba(20,49,95,.1)}@media screen and (max-width: 450px){.college-documents__header{flex-direction:column;align-items:flex-start;gap:16px}}.college-documents__title{font-size:1.5rem;font-weight:600;color:#14315f;position:relative;padding-left:24px}@media screen and (max-width: 768px){.college-documents__title{font-size:1.25rem}}@media screen and (max-width: 450px){.college-documents__title{font-size:1.3rem}}.college-documents__title::before{content:"";position:absolute;left:0;top:50%;transform:translateY(-50%);width:4px;height:70%;background:linear-gradient(187deg, #009b77 0%, #003529 85.71%);border-radius:5px}.college-documents__link{color:#009b77;font-size:1.25rem;text-decoration:none;font-weight:700;transition:all .3s ease-in-out;padding:8px 16px;border-radius:5px;border:1px solid rgba(0,0,0,0)}@media screen and (max-width: 768px){.college-documents__link{font-size:1.3rem}}@media screen and (max-width: 450px){.college-documents__link{font-size:1.125rem;align-self:flex-end}}.college-documents__link::after{content:"→";margin-left:8px;display:inline-block;transition:all .3s ease-in-out}@media(hover: hover){.college-documents__link:hover{text-decoration:none;color:rgb(0,78.5,60.2677419355)}.college-documents__link:hover::after{transform:translateX(2px)}}.college-documents__cards{display:grid;grid-template-columns:repeat(3, 1fr);gap:32px;perspective:1000px}@media screen and (max-width: 1100px){.college-documents__cards{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 990px){.college-documents__cards{grid-template-columns:repeat(2, 1fr);gap:24px}}@media screen and (max-width: 800px){.college-documents__cards{grid-template-columns:repeat(2, 1fr);gap:16px}}@media screen and (max-width: 450px){.college-documents__cards{grid-template-columns:1fr;gap:24px}}